* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.parent {
  background-color: #add8e6;
}

.admin-form {
  max-width: 25%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.admin-form > input {
  padding: 10px 20px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.admin-form > label {
  font-size: 15px;
  font-weight: 900;
}

.admin-form > button {
  padding: 5px 10px;
  color: #fff;
}

.child-container {
  /* border: 1px solid red; */
  /* margin: 20px; */

  /* display: flex; */
  /* margin: 10px auto; */
  /* flex-direction: row; */
  /* justify-content: center; */
}
.child-container > h2 {
  display: flex;
  margin-left: 20px;
}

.child-container > button {
  padding: 10px;
  color: #fff;
}

.grandChild-container {
  display: flex;
  height: 100vh;
  position: relative;
}

.grandChild-container > img {
  width: auto;
  height: 90%;
}

.grandChild-container > h2 {
  position: absolute;
  top: 90%;
  left: 30%;
}

.grandChild-container > p {
  position: absolute;
  top: 56%;
  left: 30%;
}

.container-button {
  /* border: 1px solid red; */
  padding: 10px 20px;
  position: absolute;
  top: 50%;
  color: #fff;
  border-radius: 10px;
  background-color: crimson;
}

@media screen and (max-width: 500px) {
  .grandChild-container {
    display: flex;
    height: 100vh;
    position: relative;
  }

  .grandChild-container > img {
    width: auto;
    height: 90%;
  }

  .grandChild-container > h2 {
    position: absolute;
    top: 90%;
    left: 0%;
    font-size: 15px;
    /* background-color: rebeccapurple; */
  }

  .grandChild-container > p {
    position: absolute;
    font-size: 15px;
    top: 90%;
    left: 10%;
  }
  .container-button {
    border: 1px solid red;
    padding: 10px 20px;
    position: absolute;
    top: 90%;
    left: 20%;
    color: #fff;
    font-size: 10px;
    border-radius: 10px;
    background-color: crimson;
  }

  .container > form {
    width: 100%;
    border: 1px solid #dfdfdf;
    /* padding: 20px; */
    border-radius: 5px;
    border: 1px solid red;
    background: #fff;
    padding: 80px 90px;
  }

  .field > label {
    font-size: 20px;
  }

  .field > input {
    padding: 10px 20px;
    border: 1px solid blue;

    border-radius: 10px;
    /* margin: 10px; */
  }

  .ui-form > button {
    padding: 10px;
    border-radius: 10px;
    color: #fff;
    margin-top: 10px;
  }
  pre {
    width: 70%;
    color: #fff;
    font-size: larger;
  }

  button {
    background: #0563b4 !important;
  }
  p {
    color: red;
  }
}

